
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 3.5s;
  transition: opacity 3.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-up {
  -webkit-transition: opacity 1s ease, -webkit-transform 1s ease;
  transition: opacity 1s ease, -webkit-transform 1s ease;
  transition: opacity 1s ease, transform 1s ease;
  transition: opacity 1s ease, transform 1s ease, -webkit-transform 1s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.fade-up:not(.in-viewport) {
  opacity: 0;
  -webkit-transform: translateY(15px);
          transform: translateY(15px);
}
.fade-up.in-viewport {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.fade-up.fade-up-late {
  -webkit-transition-delay: 0.4s;
          transition-delay: 0.4s;
}
.social-links {
  margin-top: 4rem;
}
.social-links span {
  display: inline-block;
  margin: 0 1rem;
  -webkit-transform: scale(0.8) translateY(1rem);
          transform: scale(0.8) translateY(1rem);
  display: inline-block;
  opacity: 0;
}
.social-links span:nth-of-type(1) {
  -webkit-transition: all 0.5s 1.5s cubic-bezier(0.31, 1.93, 0.57, 0.91);
  transition: all 0.5s 1.5s cubic-bezier(0.31, 1.93, 0.57, 0.91);
}
.social-links span:nth-of-type(2) {
  -webkit-transition: all 0.5s 1.6s cubic-bezier(0.31, 1.93, 0.57, 0.91);
  transition: all 0.5s 1.6s cubic-bezier(0.31, 1.93, 0.57, 0.91);
}
.social-links span:nth-of-type(3) {
  -webkit-transition: all 0.5s 1.7s cubic-bezier(0.31, 1.93, 0.57, 0.91);
  transition: all 0.5s 1.7s cubic-bezier(0.31, 1.93, 0.57, 0.91);
}
.social-links span:nth-of-type(4) {
  -webkit-transition: all 0.5s 1.8s cubic-bezier(0.31, 1.93, 0.57, 0.91);
  transition: all 0.5s 1.8s cubic-bezier(0.31, 1.93, 0.57, 0.91);
}
.social-links span:nth-of-type(5) {
  -webkit-transition: all 0.5s 1.9s cubic-bezier(0.31, 1.93, 0.57, 0.91);
  transition: all 0.5s 1.9s cubic-bezier(0.31, 1.93, 0.57, 0.91);
}
.social-links span:first-of-type {
  margin-left: 0;
}
.social-links span:last-of-type {
  margin-right: 0;
}
.social-links a {
  color: #fff;
  text-decoration: none;
  font-size: 2rem;
  display: inline-block;
  opacity: 0.8;
  -webkit-transition: opacity 0.2s ease, text-shadow 0.4s ease, -webkit-transform 0.2s ease;
  transition: opacity 0.2s ease, text-shadow 0.4s ease, -webkit-transform 0.2s ease;
  transition: transform 0.2s ease, opacity 0.2s ease, text-shadow 0.4s ease;
  transition: transform 0.2s ease, opacity 0.2s ease, text-shadow 0.4s ease, -webkit-transform 0.2s ease;
}
.social-links a:hover,
.social-links a:active {
  opacity: 1;
  -webkit-transform: scale(1.25) rotate(-6deg);
          transform: scale(1.25) rotate(-6deg);
  text-shadow: 0 0 1rem rgba(0,0,0,0.1);
}
.social-links.in-viewport span {
  opacity: 1;
  -webkit-transform: scale(1) translateY(0);
          transform: scale(1) translateY(0);
}


