
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 3.5s;
  transition: opacity 3.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-up {
  -webkit-transition: opacity 1s ease, -webkit-transform 1s ease;
  transition: opacity 1s ease, -webkit-transform 1s ease;
  transition: opacity 1s ease, transform 1s ease;
  transition: opacity 1s ease, transform 1s ease, -webkit-transform 1s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.fade-up:not(.in-viewport) {
  opacity: 0;
  -webkit-transform: translateY(15px);
          transform: translateY(15px);
}
.fade-up.in-viewport {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.fade-up.fade-up-late {
  -webkit-transition-delay: 0.4s;
          transition-delay: 0.4s;
}
.intro {
  padding: 8rem 0;
  text-align: center;
}
@media (max-width: 768px) {
.intro {
    padding: 3rem 0 8rem;
}
}
.intro h2 {
  color: #fff;
  font-size: 3.5rem;
  font-style: italic;
  max-width: 850px;
  max-width: 53.125rem;
  margin: 0 auto;
  font-weight: normal;
  opacity: 0;
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
  -webkit-transition: opacity 0.8s 0.2s ease, -webkit-transform 0.8s 0.2s ease;
  transition: opacity 0.8s 0.2s ease, -webkit-transform 0.8s 0.2s ease;
  transition: opacity 0.8s 0.2s ease, transform 0.8s 0.2s ease;
  transition: opacity 0.8s 0.2s ease, transform 0.8s 0.2s ease, -webkit-transform 0.8s 0.2s ease;
}
@media (max-width: 1200px) {
.intro h2 {
    max-width: 700px;
    max-width: 43.75rem;
    font-size: 2.8rem;
}
}
@media (max-width: 768px) {
.intro h2 {
    font-size: 2.2rem;
    max-width: 550px;
    max-width: 34.375rem;
}
}
@media (max-width: 500px) {
.intro h2 {
    font-size: 2rem;
    max-width: 370px;
    max-width: 23.125rem;
}
}
.intro h2.in-viewport {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}


