
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 3.5s;
  transition: opacity 3.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-up {
  -webkit-transition: opacity 1s ease, -webkit-transform 1s ease;
  transition: opacity 1s ease, -webkit-transform 1s ease;
  transition: opacity 1s ease, transform 1s ease;
  transition: opacity 1s ease, transform 1s ease, -webkit-transform 1s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.fade-up:not(.in-viewport) {
  opacity: 0;
  -webkit-transform: translateY(15px);
          transform: translateY(15px);
}
.fade-up.in-viewport {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.fade-up.fade-up-late {
  -webkit-transition-delay: 0.4s;
          transition-delay: 0.4s;
}
.portfolio-item:after {
  content: "";
  display: table;
  clear: both;
}
@media (min-width: 769px) {
.portfolio-item .column {
    float: left;
}
.portfolio-item .column:first-of-type {
    width: 35%;
    margin-right: 5%;
}
.portfolio-item .column:last-of-type {
    width: 60%;
}
}
@media (min-width: 769px) and (max-width: 1024px) {
.portfolio-item .column:first-of-type {
    width: 45%;
}
}
@media (min-width: 769px) and (max-width: 1024px) {
.portfolio-item .column:last-of-type {
    width: 50%;
}
}
.portfolio-item h3 {
  font-size: 50px;
  font-size: 3.125rem;
}
.portfolio-item .content {
  margin-top: 60px;
  margin-top: 3.75rem;
}
.portfolio-item p {
  font-size: 26px;
  font-size: 1.625rem;
  font-style: italic;
}
.portfolio-item .logo img {
  max-width: 200px;
  max-height: 110px;
  width: auto;
}
@media (max-width: 768px) {
.portfolio-item .logo img {
    max-width: 150px;
    max-height: 80px;
}
}
.portfolio-item .vv-image {
  width: 100%;
}
.portfolio-item .portfolio .vv-image {
  border-radius: 5px;
  border-radius: 0.3125rem;
  overflow: hidden;
}
.portfolio-item .portfolio:not(:last-of-type) {
  margin-bottom: 20px;
  margin-bottom: 1.25rem;
}


