
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 3.5s;
  transition: opacity 3.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-up {
  -webkit-transition: opacity 1s ease, -webkit-transform 1s ease;
  transition: opacity 1s ease, -webkit-transform 1s ease;
  transition: opacity 1s ease, transform 1s ease;
  transition: opacity 1s ease, transform 1s ease, -webkit-transform 1s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.fade-up:not(.in-viewport) {
  opacity: 0;
  -webkit-transform: translateY(15px);
          transform: translateY(15px);
}
.fade-up.in-viewport {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.fade-up.fade-up-late {
  -webkit-transition-delay: 0.4s;
          transition-delay: 0.4s;
}
.home-marquee {
  height: 100vh;
  position: relative;
}
.home-marquee .content {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  width: 95%;
  z-index: 2;
}
.home-marquee h1 {
  font-size: 11.4rem;
  font-weight: normal;
  line-height: 0.95;
  text-align: center;
  margin: 0;
  color: #fff;
}
@media (max-width: 1200px) {
.home-marquee h1 {
    font-size: 9rem;
}
}
@media (max-width: 500px) {
.home-marquee h1 {
    font-size: 7rem;
}
}
.home-marquee h1 span {
  -webkit-transform: scale(0.8) translateY(10px);
          transform: scale(0.8) translateY(10px);
  display: inline-block;
  opacity: 0;
}
.home-marquee h1 span:nth-of-type(1) {
  -webkit-transition: all 1s 0.05s cubic-bezier(0.31, 1.93, 0.57, 0.91);
  transition: all 1s 0.05s cubic-bezier(0.31, 1.93, 0.57, 0.91);
}
.home-marquee h1 span:nth-of-type(2) {
  -webkit-transition: all 1s 0.1s cubic-bezier(0.31, 1.93, 0.57, 0.91);
  transition: all 1s 0.1s cubic-bezier(0.31, 1.93, 0.57, 0.91);
}
.home-marquee h1 span:nth-of-type(3) {
  -webkit-transition: all 1s 0.15s cubic-bezier(0.31, 1.93, 0.57, 0.91);
  transition: all 1s 0.15s cubic-bezier(0.31, 1.93, 0.57, 0.91);
}
.home-marquee h1 span:nth-of-type(4) {
  -webkit-transition: all 1s 0.2s cubic-bezier(0.31, 1.93, 0.57, 0.91);
  transition: all 1s 0.2s cubic-bezier(0.31, 1.93, 0.57, 0.91);
}
.home-marquee h2 {
  background-color: #ddd6f3;
  color: #fff;
  display: inline-block;
  padding: 0.8rem 1.5rem;
  font-weight: 600;
  text-transform: uppercase;
  font-family: 'Arial', sans-serif;
  letter-spacing: 0.3vw;
  font-size: 18px;
  font-size: 1.125rem;
  border-radius: 2px;
  -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.05);
          box-shadow: 0 0 20px rgba(0,0,0,0.05);
  -webkit-transform: rotateX(90deg) translateY(10px);
          transform: rotateX(90deg) translateY(10px);
  -webkit-transition: -webkit-transform 1.4s 1s cubic-bezier(0.31, 1.93, 0.57, 0.91);
  transition: -webkit-transform 1.4s 1s cubic-bezier(0.31, 1.93, 0.57, 0.91);
  transition: transform 1.4s 1s cubic-bezier(0.31, 1.93, 0.57, 0.91);
  transition: transform 1.4s 1s cubic-bezier(0.31, 1.93, 0.57, 0.91), -webkit-transform 1.4s 1s cubic-bezier(0.31, 1.93, 0.57, 0.91);
}
@media (max-width: 500px) {
.home-marquee h2 {
    font-size: 15px;
    font-size: 0.9375rem;
    padding: 0.7rem 0.8rem;
}
}
.home-marquee.visible {
  opacity: 1;
}
.home-marquee.visible h1 span {
  -webkit-transform: scale(1) translateY(0);
          transform: scale(1) translateY(0);
  opacity: 1;
}
.home-marquee.visible h2 {
  -webkit-transform: rotateX(0) translateY(0);
          transform: rotateX(0) translateY(0);
}


