
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 3.5s;
  transition: opacity 3.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-up {
  -webkit-transition: opacity 1s ease, -webkit-transform 1s ease;
  transition: opacity 1s ease, -webkit-transform 1s ease;
  transition: opacity 1s ease, transform 1s ease;
  transition: opacity 1s ease, transform 1s ease, -webkit-transform 1s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.fade-up:not(.in-viewport) {
  opacity: 0;
  -webkit-transform: translateY(15px);
          transform: translateY(15px);
}
.fade-up.in-viewport {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.fade-up.fade-up-late {
  -webkit-transition-delay: 0.4s;
          transition-delay: 0.4s;
}
.link-banner {
  -webkit-transform: rotateX(90deg) translateY(10px);
          transform: rotateX(90deg) translateY(10px);
  -webkit-transition: -webkit-transform 1.4s 1.5s cubic-bezier(0.31, 1.93, 0.57, 0.91);
  transition: -webkit-transform 1.4s 1.5s cubic-bezier(0.31, 1.93, 0.57, 0.91);
  transition: transform 1.4s 1.5s cubic-bezier(0.31, 1.93, 0.57, 0.91);
  transition: transform 1.4s 1.5s cubic-bezier(0.31, 1.93, 0.57, 0.91), -webkit-transform 1.4s 1.5s cubic-bezier(0.31, 1.93, 0.57, 0.91);
}
.link-banner.in-viewport {
  -webkit-transform: rotateX(0) translateY(0);
          transform: rotateX(0) translateY(0);
}
.link-banner a {
  background: #fff;
  font-family: 'Arial', sans-serif;
  display: inline-block;
  padding: 10px 10px;
  padding: 0.625rem 20px;
  padding: 0.625rem 1.25rem;
  position: relative;
  margin-top: 50px;
  margin-top: 3.125rem;
  -webkit-box-shadow: 0 10px 30px rgba(0,0,0,0.1);
          box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  -webkit-transform-origin: center center;
          transform-origin: center center;
  cursor: pointer;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.link-banner a:hover {
  -webkit-transform: translateY(-5px) scale(1.03) rotate(-1deg);
          transform: translateY(-5px) scale(1.03) rotate(-1deg);
  -webkit-box-shadow: 0 30px 40px rgba(0,0,0,0.05);
          box-shadow: 0 30px 40px rgba(0,0,0,0.05);
}
.link-banner a * {
  color: #c0b3e9;
  letter-spacing: 1px;
  letter-spacing: 0.0625rem;
  text-transform: uppercase;
  font-size: 14px;
  font-size: 0.875rem;
}
@media (max-width: 768px) {
.link-banner a * {
    font-size: 12px;
    font-size: 0.75rem;
}
}
@media (max-width: 500px) {
.link-banner a * {
    letter-spacing: 0;
}
}
.link-banner a a {
  font-weight: bold;
  margin-left: 5px;
}
.link-banner a .arrows {
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
}
.link-banner a .arrows:before,
.link-banner a .arrows:after {
  content: ' ';
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
}
.link-banner a .arrows-left {
  left: -19px;
  left: -1.1875rem;
}
.link-banner a .arrows-left:before {
  top: 0;
  border-width: 0 20px 20px 0;
  border-width: 0 1.25rem 20px 0;
  border-width: 0 1.25rem 1.25rem 0;
  border-color: transparent #fff transparent transparent;
}
.link-banner a .arrows-left:after {
  bottom: 0;
  border-width: 0 0 20px 20px;
  border-width: 0 0 1.25rem 20px;
  border-width: 0 0 1.25rem 1.25rem;
  border-color: transparent transparent #fff transparent;
}
.link-banner a .arrows-right {
  right: 1px;
}
.link-banner a .arrows-right:before {
  bottom: 0;
  border-width: 0 20px 20px 0;
  border-width: 0 1.25rem 20px 0;
  border-width: 0 1.25rem 1.25rem 0;
  border-color: transparent transparent #fff transparent;
}
.link-banner a .arrows-right:after {
  top: 0;
  border-width: 0 0 20px 20px;
  border-width: 0 0 1.25rem 20px;
  border-width: 0 0 1.25rem 1.25rem;
  border-color: transparent transparent transparent #fff;
}


